@import url("https://fonts.googleapis.com/css2?family=Alice&display=swap");

.weather-app {
  margin: 0 auto;
  padding: 16px;
  max-height: 350px;
  color: #3f3f44;
}
.container {
  font-family: "Alice", serif;
  margin-top: 16px;
  padding: 32px;
  max-width: 640px;
  border-radius: 16px;
  background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
}

.form-control {
  border: none;
}

.card {
  color: white;
  margin-top: 32px;
  border-radius: 12px;
  border: none;
  background-color: white;
  color: #3f3f44;
}
.card-body {
  padding: 32px;
  font-size: 20px;
  text-align: center;
}
.weather-forecast-temperature {
  font-size: 18px;
  text-align: center;
  padding-top: 8px;
  white-space: nowrap;
}

#city {
  white-space: nowrap;
}
h1 {
  font-size: 30px;
}
h3,
li {
  font-size: 18px;
}
li {
  list-style: none;
}
ul {
  margin: 0;
  padding: 0;
}

.temperature {
  position: relative;
  top: -24px;
  font-size: 60px;
}

small {
  font-size: 20px;
  vertical-align: super;
}

.fa-map-marker-alt {
  font-size: 24px;
}

.btn {
  cursor: pointer;
  height: calc(1.5em + 0.75rem + 2px);
  transition: all 200ms ease-in-out;
  border: none;
  margin-right: 15px;
  color: #3f3f44;
  background-color: #4facfe;
}
.btn:hover {
  background-color: #00f2fe;
}

.container .row > img {
  margin-top: 16px;
  max-width: 256px;
  max-height: 288px;
}

.card-body img {
  height: 64px;
  max-width: 64px;
  margin: 12px 0;
}

a {
  cursor: pointer;
  color: inherit;
  text-decoration: underline;
}
a:hover {
  color: #00f2fe;
}

.coded {
  margin: 10px;
  display: block;
  text-align: center;
  font-size: 16px;
}

#humidity,
#wind {
  font-weight: bold;
}
#description::first-letter {
  text-transform: capitalize;
}

@media (max-width: 480px) {
  .container {
    font-size: x-small;
    padding: 16px;
    margin-top: 0;
    border-radius: 0;
  }

  .btn {
    max-width: 72px;
  }

  .container .row > img {
    margin: 0;
    position: relative;
    left: -16px;
    width: 50%;
  }

  .card {
    border-radius: 8px;
  }
  .card-body {
    padding: 16px;
    font-size: 16px;
  }
  .card-body img {
    max-width: 40px;
    margin: 0;
    vertical-align: top;
  }
  .card-body .col-2 {
    padding: 0;
  }
}
